.menu {
    width: 15%;
    height: 100%;
    background-color: rgb(241, 241, 241);
    padding-left: 0;
    position: fixed;
}

.menu > a {
    display: block;
    padding: 5px 5px 5px 15px;
    color: #000;
    text-decoration: none;
}

.menu > a:hover {
    background-color: lightgrey;
    cursor: pointer;
}

.activeLesson {
    background-color: lightgrey;
}

@media only screen and (max-width: 768px) {
    .menu {
        width: 100%;
        position: relative;
        margin-top: 48px;
    }
}


